import './bootstrap.js';

// Importation des bibliothèques nécessaires
import { createApp } from 'vue'; // Importation de Vue

import App from './components/App.vue'; // Le composant racine de l'application

// Importation des composants globaux (si nécessaire)
import HelloWorld from './components/HelloWorld.vue'; // Un exemple de composant
import UserList from './components/users/UserList.vue';

// Créer une instance de l'application Vue
const app = createApp(App);

// Enregistrer les composants globaux
app.component('hello-world', HelloWorld); // Enregistrer le composant HelloWorld
app.component('user-list', UserList);

// Monter l'application sur un élément HTML
app.mount('#app'); // Le point d'entrée de l'application
